<template>
  <div class="about">
    <h1>This is an about page</h1>
    <h5>{{token}}</h5>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  computed: {
    ...mapState(["token"])
  },
  methods: {
    async getOrders(){
      try {
        const request = await fetch('http://localhost:8090/api/order', {
          headers: {
            'Authorization': 'Bearer ' + this.token
          }
        });

        const response = await request.json()
        console.log(response)

      } catch (error) {
        
      }
    }
  },
  created(){
    this.getOrders()
  }
 }
</script>